import React from "react";
import { Helmet } from "react-helmet";

import SeedProvider from "providers/Seed";

import "config/themes/_plinko.scss";

import PlinkoPage from "./page";

const PlinkoWrapper = () => {
  return (
    <SeedProvider gameName="plinko">
      <Helmet>
        <title>Plinko | Crypto Gambling Casino</title>
        <meta name="title" content="Play Bitcoin Plinko - Pasino.com" />
        <meta
          name="description"
          content="Let the Plinko ball drop and win huge prizes in DOGE, ETH, Litecoin, and many other cryptocurrencies on pasino.com. "
        />
        <meta
          name="keywords"
          content="Plinko Game, Plinko Gambling, Bitcoin Plinko, Play plinko crypto, "
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>

      <PlinkoPage />
    </SeedProvider>
  );
};

export default React.memo(PlinkoWrapper);
