import React, {
  useCallback,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
} from "react";

import { Statistics, notifyBarManager } from "components";
import { track, fetchBase, QueryOptions } from "utils";
import { useFetch, useLocalStorage } from "hooks";
import { throttle } from "utils/lodash";

import LazyComponentLoader from "config/LazyComponentLoader";
import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";
import { HotKeysModal } from "components/HotKeys";
import LiveStats from "components/LiveStats";

import type PlinkoEngineType from "games/Plinko";
import { resetFocus } from "utils/resetFocus";

import { useSoundSystemControl } from "components/SoundModal/Provider";
import { SoundModals } from "components/SoundModal";
import {
  gamePathNames,
  getSoundEffectNames,
} from "components/SoundModal/Provider/config";

import { Controller, ControllerProps, Game } from "./components";
import classes from "./page.module.scss";

const BettingInformation = React.lazy(() =>
  LazyComponentLoader(import("pages/ModalPages/pages/BettingInformation")),
);

export interface ConfigurationData {
  success: boolean;
  message: string;
  coins: {
    coin: string;
    minimum_bet: string;
    maximum_bet: string;
    maximum_profit: string;
  }[];
  multipliers_table: Record<string, Record<string, number[]>>;
}

export interface PlayBetData {
  success: boolean;
  message: string;
  payout: number;
  balance: string;
  win: number;
  roll_number: number;
}

const fetchPlayBet = (options: QueryOptions<PlayBetData>) =>
  fetchBase<PlayBetData>("plinko/play", options);

const interimProfitValue = {
  profit: 0,
  amount: 0,
};

const profitUpdateWait = throttle((f: () => void) => {
  f();
}, 400);

const PlinkoPage = () => {
  const [initialized, setInitialized] = useState(false);
  const [multipliers, setMultipliers] = useState<number[]>([]);

  const [PlinkoEngine, setPlinkoEngine] = useState<PlinkoEngineType>();
  const [colorScheme] = useLocalStorage("color-scheme");

  const gameBlockRef = useRef<HTMLDivElement>(null);
  const Plinko = useRef<PlinkoEngineType>();
  const isFocused = useRef<boolean>();

  const [fetchConfiguration, { data: configurationData }] =
    useFetch<ConfigurationData>("plinko/get-configuration");

  const [isSoundOpen, setIsSoundOpen] = useState(false);
  const [isLiveStatOpen, setIsLiveStatOpen] = useState(false);
  const [isHotkeyOpen, setIsHotkeyOpen] = useState(false);
  const [isBetInfoOpen, setIsBetInfoOpen] = useState(false);
  console.log(multipliers);

  const {
    controlFunctions: { playEffect },
    soundSystemState,
  } = useSoundSystemControl();
  const { hashDice, end, start } = getSoundEffectNames(gamePathNames.PLINKO);
  const soundEffects = {
    hashDice: () => playEffect(hashDice),
    end: () => playEffect(end),
    start: () => playEffect(start),
  };
  useEffect(() => {
    fetchConfiguration();
  }, [fetchConfiguration]);

  useEffect(() => {
    const onFocus = () => {
      isFocused.current = true;
    };

    const onBlur = () => {
      isFocused.current = false;
    };

    onFocus();

    window.addEventListener("focus", onFocus);
    window.addEventListener("blur", onBlur);

    return () => {
      onBlur();
    };
  }, []);

  useLayoutEffect(() => {
    if (!multipliers?.length || !PlinkoEngine) {
      return;
    }

    if (gameBlockRef.current && !Plinko.current) {
      Plinko.current = new (PlinkoEngine as any)({
        container: gameBlockRef.current,
      });
    }

    if (Plinko.current) {
      Plinko.current.init(multipliers, () => {
        setInitialized(true);
      });

      //@ts-ignore
      window.testPlinko = (cell: number, tValue: number) => {
        Plinko.current?.playBet(cell, () => {}, tValue);
      };
    }

    return () => {
      if (Plinko.current) {
        Plinko.current.clearEnvironment();
      }
    };
  }, [initialized, multipliers, PlinkoEngine, colorScheme]);

  useLayoutEffect(() => {
    if (gameBlockRef.current && Plinko.current) {
      Plinko.current.soundEffects = soundEffects;
    }
  }, [
    initialized,
    soundSystemState.isGameEffectsPlay,
    playEffect,
    soundEffects,
  ]);

  const handleBet: ControllerProps["handleBet"] = async (
    { amount, pins, risk, coin, seed },
    clear,
  ) => {
    let resultData: PlayBetData | undefined;
    const variables = {
      bet_amt: Number(amount).toFixed(8),
      coin,
      pins,
      risk_multiplier: risk,
      client_seed: seed,
    };

    try {
      const { data: playBetData, error } = await fetchPlayBet({
        variables,
        withoutLoading: true,
      });

      resultData = playBetData;

      if (error || !resultData?.success) {
        throw error;
      }

      if (resultData && isLiveStatOpen) {
        interimProfitValue.profit +=
          Number(variables.bet_amt) * resultData.payout -
          Number(variables.bet_amt);
        interimProfitValue.amount += Number(variables.bet_amt);

        profitUpdateWait(() => {
          liveStatsManager.addBet({
            profit: interimProfitValue.profit,
            amount: interimProfitValue.amount,
            coin: variables.coin,
          });

          interimProfitValue.profit = 0;
          interimProfitValue.amount = 0;
        });
      }

      if (
        isFocused.current &&
        Plinko.current &&
        initialized &&
        playBetData &&
        (playBetData.roll_number || playBetData.roll_number === 0) &&
        playBetData.roll_number < multipliers.length &&
        playBetData.roll_number > 0
      ) {
        Plinko.current.playBet(playBetData.roll_number);
      }
    } catch (error) {
      track()?.errors(error);

      if (clear) {
        clear();
      }
      const message = `
${error}
Plinko > page > handleBet
MessageEvent: ${JSON.stringify(variables)}`;

      try {
        track()?.errors(message);
        notifyBarManager.error({
          message: error as string,
        });
      } catch (error) {
        console.log("ðŸš€ trackError", error, variables);
      }
    }

    return resultData;
  };

  const handleLiveStatChange = useCallback(() => {
    setIsLiveStatOpen(!isLiveStatOpen);
  }, [isLiveStatOpen]);
  const handleLiveStatClose = useCallback(() => {
    setIsLiveStatOpen(false);
  }, []);
  const handleHotKeysChange = () => {
    resetFocus();
    setIsHotkeyOpen(!isHotkeyOpen);
  };
  const handleSoundChange = () => {
    resetFocus();
    setIsSoundOpen(!isSoundOpen);
  };
  const handleHotKeysClose = useCallback(() => {
    setIsHotkeyOpen(false);
  }, []);
  const handleSoundClose = () => {
    setIsSoundOpen(false);
  };

  return (
    <div className={classes.root}>
      <SoundModals isOpen={isSoundOpen} onClose={handleSoundClose} />
      <LiveStats isOpen={isLiveStatOpen} handleClose={handleLiveStatClose} />
      <BettingInformation
        isOpen={isBetInfoOpen}
        onClose={() => setIsBetInfoOpen(false)}
        game="plinko"
      />
      <HotKeysModal isOpen={isHotkeyOpen} onClose={handleHotKeysClose} />

      <Game setEnginRef={setPlinkoEngine} />
      <div className={classes.item}>
        <div className={classes.gameBlock}>
          <div className={classes.gameBlockBackground}></div>
          <div className={classes.gameWrapper}>
            <div className={classes.game} ref={gameBlockRef} />
          </div>
          <div className={classes.gameControllerWrapper}>
            <Controller
              handleBet={handleBet}
              configurationData={configurationData}
              setMultipliers={setMultipliers}
              handleSoundChange={handleSoundChange}
              handleHotKeysChange={handleHotKeysChange}
              handleLiveStatChange={handleLiveStatChange}
              handleBetInfoChange={() => setIsBetInfoOpen(true)}
            />
          </div>
        </div>
      </div>
      <div className={classes.item}>
        <div className={classes.statisticsWrapper}>
          <Statistics withFairness />
        </div>
      </div>
    </div>
  );
};

export default React.memo(PlinkoPage);
